<template>
	<div class="orderbackWrap">
		<Pagehead txt=''></Pagehead>
		<p class="img"><img src="../assets/images/ic_hottest.png"></p>
		<p class="notify"><span class="name">{{username}}</span></p>
		<h4><span>{{price}}</span></h4>
		<h4></h4>
		<p class="info"></p>
		<div class="icon">
			<button type="button"><router-link to='/home'></router-link></button>
			<button type="button" class="orderdetail"><router-link to='/orderdetails'></router-link></button>
		</div>
		<dl>
			<dt></dt>
			<dd>1.</dd>
			<dd>2.</dd>
			<dd>3.</dd>
			<dd>4.</dd>
		</dl>
	</div>
</template>

<script>
	import Bus from '../bus.js'
	import Pagehead from './pagehead.vue'
	export default {
		components:{
			Pagehead
		},
		data:function(){
			return{
				username:"sunshine123",
				price:''
			}
		},
		created:function(){
			var that=this;
			Bus.$on('tprice',function(val){
				that.price = val;
			});

		}
	}
</script>

<style>
	.orderbackWrap .notify{font-size: 0.3rem;padding: 0.1rem;}
	.orderbackWrap .name{color: #33CCFF;}
	.orderbackWrap h4{font-size: 0.35rem;padding: 0.1rem 0;}
	.orderbackWrap h4>span{color: #FE4C4C;}
	.orderbackWrap .info{font-size: 0.35rem;color: #ccc;padding:0.2rem 0; }
	.orderbackWrap .icon{display: flex;justify-content: space-around;padding-top: 0.1rem;}
	.orderbackWrap button{padding: 0.15rem 0.6rem;border: none;color:#fff;background: #ccc;border-radius: 0.1rem;font-size: 0.3rem; }
	.orderbackWrap .orderdetail{background:#FE4C4C; }
	.orderbackWrap dl{font-size: 0.2rem;color: #999;padding:1rem 0.5rem 0;text-align: left;}
	.orderbackWrap dl dt{padding: 0.1rem;font-size: 0.25rem;}
	.orderbackWrap a{color: #fff;}


	
</style>


